<template>
  <div class="text-h6">Control method: <q-btn-toggle
        v-model="inputType"
        spread
        no-caps
        toggle-color="primary"
        @click="inputSwitch()"
        :options="[
          {label: 'Keyboard', value: 0},
          {label: 'Gamepad', value: 1},
          {label: 'Web', value: 2}
        ]"
      /></div>
  <div class="text-h6">Use the buttons below to control the DeepPicar</div>
  <div> Throttle: <q-knob
      show-value
      v-model="throttle"
      size="80 px"
    /></div>
  <div class="row full-width justify-between">
    <q-btn
      color="primary"
      size="xl"
      padding="10px 50px"
      @mousedown="actuate('left', throttle)"
      @touchstart="actuate('left', throttle)"
      @mouseup="actuate('center', throttle)"
      @touchend="actuate('center', throttle)"
      icon="arrow_left"
    />
    <div class="flex-break"></div>

    <div class="column">
      <q-btn round color="primary" size="xl" @click="actuate('forward', throttle)" icon="north" />
      <q-btn round color="primary" size="xl" @click="actuate('stop', throttle)" icon="front_hand" />
      <q-btn round color="primary" size="xl" @click="actuate('reverse', throttle)" icon="south" />
    </div>
    <div class="flex-break"></div>
    <q-btn
      color="primary"
      size="xl"
      padding="10px 50px"
      @mousedown="actuate('right', throttle)"
      @touchstart="actuate('right', throttle)"
      @mouseup="actuate('center', throttle)"
      @touchend="actuate('center', throttle)"
      icon="arrow_right"
    />
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'ControlPad',
  data: function () {
    return {
      throttle: 50,
      inputType: 2,
    };
  },
  created: function () {
    document.addEventListener('keydown', this.keyDown);
    document.addEventListener('keyup', this.keyUp);
  },
  watch: {
    throttle(value) {
      this.actuate('', value);
    },
  },
  methods: {
    actuate: function (direction, throttle) {
      axios.post('http://' + window.location.hostname + ':8000/actuate', { params: { direction: direction, speed: throttle } })
        .then(response => this.responseData = response.data);
    },
    inputSwitch: function () {
      axios.post('http://' + window.location.hostname + ':8001/input_switch', { params: { input_type: this.inputType } })
        .then(response => this.responseData = response.data);
    },
    keyDown: function (k) {
      console.log(k)
      if (k.key == 'ArrowUp') {
        this.actuate('forward', this.throttle);
      }
      if (k.key == 'ArrowLeft') {
        this.actuate('left', this.throttle);
      }
      if (k.key == 'ArrowRight') {
        this.actuate('right', this.throttle);
      }
      if (k.key == 'ArrowDown') {
        this.actuate('reverse', this.throttle);
      }
      if (k.key == ' ') {
        this.actuate('stop', this.throttle);
      }
    },
    keyUp: function (k) {
      if (k.key == 'ArrowLeft' || k.key == 'ArrowRight') {
        this.actuate('center', this.throttle);
      }
    }
  }
}
</script>
