<template>
    <div class="text-h6">Begin and stop recording</div>
    <div>
        <q-btn-group outline>
            <q-btn outline size="lg" v-if="!recording" @click="record('begin');recording=!recording" label="Record" />
            <q-btn outline size="lg" v-else @click="record('finish');recording=!recording" label="Finish" />
        </q-btn-group>
    </div>
    <div class="text-h6">
        Download :
        <q-btn size="lg" @click="download()" icon="download" />
    </div>
    <div class="text-h6">
        Upload:
        <q-uploader
            :url="this.mainApi + 'upload'"
            label="tflite upload"
            send-raw
        />
    </div>
    <div class="text-h6">Start and stop DNN</div>
    <div>
        <q-btn-group outline>
            <q-btn outline size="lg" v-if="!running" @click="dnn('start');running=!running" label="Start DNN" />
            <q-btn outline size="lg" v-else @click="dnn('stop');running=!running" label="Stop DNN" />
        </q-btn-group>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'DataMenu',
    data() {
        return {
            inputApi: 'http://' + window.location.hostname + ':8000/',
            mainApi: 'http://' + window.location.hostname + ':8001/',
            recording: false,
            running: false,
        }
    },
    methods: {
        record: function (action) {
            axios.post(this.inputApi + 'record', { params: { action: action } })
                .then(response => this.responseData = response.data);

        },
        download: function () {
            window.open(this.mainApi + 'download');
        },
        dnn: function (action) {
            axios.post(this.inputApi + 'dnn', { params: { action: action } })
                .then(response => this.responseData = response.data)
        }
    }
}
</script>
