<template>
  <div>
    <img v-if="video" :src="picam" :key="NaN" />
    <q-btn-toggle
        v-model="video"
        spread
        no-caps
        toggle-color="primary"
        @click="streaming()"
        :options="[
          {label: 'Enable Video', value: true},
          {label: 'Disable Video', value: false}
        ]"
      />
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'CameraView',
  data: function () {
    return {
      video: true,
      picam: "",
    };
  },
  created() {
    this.picam = 'http://' + window.location.hostname + ':8001/stream.mjpg';
  },
  methods: {
        streaming: function () {
            axios.post(this.picam, { params: { streaming: this.video } })
                .then(response => this.responseData = response.data);

        },
  }
};
</script>
